@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;0,700;&display=swap');

body {
    width: 1440px;
    height: 800px;
    background-color: hsl(30, 38%, 92%);
    display: flex;
    flex-direction: row;
}

div {
    & img {
        width: 300px;
        height: 450px;
        margin-left: 468px;
        margin-top: 175px;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
    }
}

main {
    display: flex;
    flex-direction: row;
    margin-top: 175px;
    background-color: hsl(0, 0%, 100%);
    width: 300px;
    height: 450px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

article {
    display: flex;
    flex-direction: column;
    margin-left: 32px;
    margin-top: 14px;
}

section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
p{
    font-family: 'Montserrat', sans-serif;
    color: hsl(228, 12%, 48%); 
    margin-bottom: 0px;
    font-size: 14px;
    line-height: 23px;
    max-width: 230px;
    letter-spacing: 1px;
}
p1{
    color: hsl(0, 0%, 100%);
    font-size: 16px;
    margin-left: 12px;
    font-weight: 500;
}
button{
    width: 236px;
    height: 47.5px;
    background-color: hsl(158, 36%, 37%);
    border: none;
    border-radius: 8px;
    margin-top: 0.6px;
    cursor: pointer;
    display: flex;
    flex-direction: row;   
    align-items: center;
    & img {
        margin-left: 56.8px;
    }
}
p2{
    color: hsl(228, 12%, 48%); 
    font-size: 12.8px;
    text-decoration: line-through;
    margin-right: 43px;
    font-family: 'Fraunces';
    margin-top: -3px;
}
p3{
    color: hsl(228, 12%, 48%); 
    margin-bottom: 2px;
    margin-top: 18.5px;
    font-size: 12px;
    letter-spacing: 5.4px;
    font-family: 'Montserrat', sans-serif;
}
h1{
    font-family: "Fraunces";
    color: hsl(212, 21%, 14%);
    font-size: 32px;
    margin-top: 19px;
    margin-bottom: 13px;
    line-height: 31.4px;
}
h2{
    font-family: "Fraunces";
    color: hsl(158, 36%, 37%);
    font-size: 32px;
    margin-top: 25px;
    margin-bottom: 25px;
}
